body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}


.banner__letter {
	opacity: 0;
	-webkit-filter: blur(4px);
	        filter: blur(4px);
	-webkit-transform: perspective(200px) translateZ(80px);
	        transform: perspective(200px) translateZ(80px);
	-webkit-transition: opacity .5s ease, -webkit-transform 1.6s ease, -webkit-filter 1.6s ease;
	transition: opacity .5s ease, -webkit-transform 1.6s ease, -webkit-filter 1.6s ease;
	transition: opacity .5s ease, transform 1.6s ease, filter 1.6s ease;
	transition: opacity .5s ease, transform 1.6s ease, filter 1.6s ease, -webkit-transform 1.6s ease, -webkit-filter 1.6s ease;
	display: inline-block;
	min-width: 8px;
}

.banner__letter.is-active {
	opacity: 1;
	-webkit-filter: blur(0px);
	        filter: blur(0px);
	-webkit-transform: perspective(200px) translateZ(0px);
	        transform: perspective(200px) translateZ(0px);
}
@font-face {
  font-family: 'Qardoos';
  font-style: normal ;
  font-weight: normal;
  src: url(/static/media/Qardoos.18e9fc29.otf) format('open-type');
}

.noto {
  font-family: 'Noto Serif TC', serif;
}

h1 {
  font-size: 70px;
  font-weight: 100;
  letter-spacing: 1.9px;
  margin: 10px 0px;
}



h2 {
  font-size: 36px;
  letter-spacing: .5px;
  margin: 8px 0px;
}

.banner {
  display: flex;
  align-items: center;
  justify-content: flex-end;
  padding: 50px;
  height: calc(100vh - 100px);
}

.banner__wrap {
  text-align: left;
}

.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

